.swiper {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
  max-width: 1024px;
  max-height: 720px;
  min-height: clamp(252px, 3.417rem + 52.33vw, 548px);
}

.swiper-component-wrapper .swiper-wrapper {
  height: auto;
  transition: all 0.3s ease-out;
  opacity: 1;
}
.swiper-component-wrapper .swiper-wrapper.hide {
  opacity: 0;
}

.swiper-slide img {
  height: 100%;
  width: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-pagination {
  transition: all 0.3s ease-out;
}

.swiper-pagination-bullet {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  color: #F6F7F7;
  line-height: 1.4;
  font-size: 20px;
  opacity: 1;
  background-color: #283935;
  transition: all 0.3s ease-out;
}

.swiper-pagination-bullet-active {
  color: #171C1A;
  background-color: #F6F7F7;
}

.swiper-pagination-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 360px;
  height: 72px;
  padding-top: 12px;
  overflow: hidden;
  background-color: #182825;
  opacity: 1;
  z-index: 2;
  transition: all 0.3s ease-out;
}
.swiper-pagination-wrapper .swiper-pagination {
  position: absolute !important;
  width: -webkit-max-content !important;
  width: max-content !important;
}
.swiper-pagination-wrapper .swiper-pagination {
  display: flex;
  gap: 12px;
  bottom: 0;
}

.swiper-component-wrapper .swiper-pagination-bullet.margin-0 {
  margin: 0 !important;
}

